<template>
  <div>
    <div class="search">
      <input
      type="text"
      class="search-input"
      placeholder="输入城市名或拼音"
      v-model="keyword"
      >
    </div>
    <div class="search-content" ref="wrapper" v-show="keyword">
      <ul>
        <li
        class="search-item border-bottom"
        v-for="item of list"
        :key="item.id"
        @click="handleCityClick(item.name)"
        >
          {{item.name}}
        </li>
        <li class="search-item border-bottom" v-show="showItem">
          找不到匹配的城市
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import { mapMutations } from 'vuex'
export default {
  name: 'CitySearch',
  props: {
    cities: Object
  },
  data () {
    return {
      keyword: '',
      timer: null,
      list: []
    }
  },
  computed: {
    showItem () {
      return !this.list.length
    }
  },
  watch: {
    keyword () {
      if (this.timer) {
        clearTimeout(this.timer)
      }
      if (!this.keyword) {
        this.list = []
        return
      }
      this.timer = setTimeout(() => {
        const result = []
        for (let i in this.cities) {
          this.cities[i].forEach((value) => {
            if (value.spell.indexOf(this.keyword.toLowerCase()) > -1 || value.name.indexOf(this.keyword) > -1) {
              result.push(value)
            }
          })
        }
        this.list = result
      }, 100)
    }
  },
  methods: {
    handleCityClick (city) {
      this.changeCity(city)
      this.$router.push('/')
    },
    ...mapMutations(['changeCity'])
  },
  mounted () {
    this.scroll = new BScroll(this.$refs.wrapper)
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/assets/styles/varibles.styl'
  .search
    background-color: $bgColor
    text-align: center
    padding: .1rem .2rem
    .search-input
      width: 100%
      height: .6rem
      border-radius: .06rem
      text-align: center
      color: #666
      padding: 0 .2rem
      box-sizing: border-box
  .search-content
    position: absolute
    overflow: hidden
    top: 1.66rem
    right: 0
    bottom: 0
    left: 0
    z-index: 99
    background-color: #eee
    .search-item
      line-height: .8rem
      padding-left: .2rem
      background-color: #fff
      color: #666
</style>



