<template>
  <div class="weekend">
    <div class="title">周末去哪儿</div>
    <ul>
      <li class="item" v-for="item of list" :key="item.id">
        <div class="item-img-wrapper">
          <img :src="item.imgUrl" class="item-img">
        </div>
        <div class="item-info">
          <h3 class="item-title">{{item.title}}</h3>
          <p class="item-desc">{{item.desc}}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'HomeWeekend',
  props: {
    list: Array
  }
}
</script>

<style lang="stylus" scoped>
@import "~@/assets/styles/mixins.styl"
  .title
    line-height: .6rem
    padding-left: .2rem
    background-color #eee
  .item
    padding-bottom: .2rem
    .item-img-wrapper
      overflow: hidden
      height: 0
      padding-bottom: 37.09%
      .item-img
        width: 100%
    .item-info
      padding-left: .2rem
      background-color: #fff
      .item-title
        line-height: .56rem
        font-size: .3rem
        font-weight: bold
        background-color: #fff
        ellipsis()
      .item-desc
        color: #999
        line-height: .46rem
        ellipsis()
</style>



