<template>
  <div class="list">
    <div class="item" v-for="(item, index) of list" :key="index">
      <div class="item-title border-bottom">
        <span class="iconfont iconfont-ticket">&#xe602;</span>
        {{item.title}}
      </div>
      <div>
        <div v-if="item.children" class="item-children">
          <detail-list :list="item.children"></detail-list>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailList',
  props: {
    list: Array
  }
}
</script>

<style lang="stylus" scoped>
  .item-title
    line-height: .6rem
    font-size: .32rem
    .iconfont-ticket
      padding: 0 .2rem
  .item-children
    padding-left: .2rem
</style>



