<template>
  <div class="header">
    <div class="header-left">
      <span class="iconfont iconfont-back">&#xe624;</span>
    </div>
    <div class="header-input">
      <span class="iconfont iconfont-search">&#xe632;</span>
      <input type="text" class="search-input" placeholder="输入城市/景点/游玩主题">
    </div>
    <router-link to="/city">
      <div class="header-right">
        {{this.city}}
        <span class="iconfont">&#xe6aa;</span>
      </div>
    </router-link>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'HomeHeader',
  computed: {
    ...mapState(['city'])
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/assets/styles/varibles.styl'
  .header
    overflow: hidden
    height: .86rem
    background-color: $bgColor
    display: flex
    align-items: center
    color: #fff
  .header-left
    float:left
    margin-left: .2rem
    color: #fff
  .header-input
    flex: 1
    height: .6rem
    margin: 0 .2rem
    background-color: #fff
    border-radius: .05rem
    color: #666
    position: relative
    .iconfont-search
      position: absolute
      left: .1rem
      top: .12rem
    .search-input
      width: 100%
      height: 100%
      padding-left: 0 .12rem
      text-align: center
      border-radius: .05rem
  .header-right
    float: right
    margin-right: .12rem
    color: #fff
</style>



